___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: "Poppins", sans-serif;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 10rem auto;
  border: 1px solid #ddd;
  box-shadow: 0 2px 4px rgba(133, 128, 128, 0.4);
  background-color: white;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 25px;
  font-weight: 300;
  border-bottom: 1px solid #ccc;
}

.list-add {
  display: flex;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  justify-content: space-between;
}

.addtasks {
  font-style: italic;
  font-size: 18px;
  outline: none;
  width: 90%;
  border: none;
}

.clear-button {
  padding: 10px;
  background-color: #ccc;
  color: #000;
}

.clear-button:hover {
  cursor: pointer;
  background-color: rgb(5, 184, 5);
  color: #fff;
}

.todo-list {
  padding: 0;
  display: flex;
  flex-direction: column;
}

.list-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 98%;
  border-bottom: 1px solid #ccc;
}

.list-item {
  list-style-type: none;
  padding: 12px;
  width: auto;
}

.btn {
  background-color: transparent;
  border: none;
}

input[type="checkbox"] {
  margin-right: 1rem;
}

.trash {
  cursor: pointer;
  color: red;
}

input {
  border: none;
}

.strike {
  text-decoration: line-through;
  color: #a5a2a2;
}
